/***** Wootric overrides CSS *****/

#wootric-modal .wootric-beacon-parent #wootric-question p,
#wootric-modal #wootric-thanks p{
  color: #4A4A4A !important;
}

#wootric-modal li{
  border-color: #CDD8DF !important;
  color: #4A4A4A !important
}

#wootric-modal label{
  color: #959EA3 !important;
}

#wootric-modal #wootric-submit{
  color: #FFF !important;
  background-color: #4480C5 !important;
}

#wootric-modal li.selected, #wootric-modal li.selected:hover{
  color: #FFF !important;
  background-color: #40B186 !important;
  border-color: #40B186 !important;
}

#wootric-modal textarea{
  color: #959EA3 !important;
  border-color: #CDD8DF !important;
  background-color: #FFF !important;
}

#wootric-modal #wootric-close:focus, #wootric-modal #wootric-close:hover{
  width: 100px !important;
}

.rccs__name {
  font-size: 13px;
  width: 50%;
}

@media(min-width: 960px) and (max-width: 1035px){
  .rccs__card{
    margin-left: -15px;
    transform: scale(0.9);
  }
}

.styles_bottomInfoWrapper__O5elh{
  display: flex;
  grid-gap: 16px;
  gap: 16px;
  margin-top: 32px;
}

